






























import { defineComponent } from "@vue/composition-api";

export default defineComponent({
  name: "ProductsSlider",
  props: {
    products: {
      type: Array,
      required: true
    }
  },
  setup(_, { emit }) {
    const toggle = (item) => {
      emit("on-product-selection", item);
    };
    return {
      toggle
    };
  }
});
